import React from 'react'
import { Button, Card, Heading } from 'react-bulma-components'
import { FaStreetView } from 'react-icons/fa'
import { useDatabase, useDatabaseObjectData } from 'reactfire'

export const Home = () => {
  const ref = useDatabase().ref('home')
  const { data }: any = useDatabaseObjectData(ref)

  return (
    <div className='container-fluid'>
      <header>
        <div className='main-header'>
          <Heading>Get Your Flags</Heading>
          <Button
            renderAs='a'
            href='https://www.google.com/maps/place/801+W+Palmetto+Park+Rd,+Boca+Raton,+FL+33486/@26.350875,-80.1050591,17z/data=!3m1!4b1!4m5!3m4!1s0x88d91df97114f9ab:0xda749d3e84a1a3e7!8m2!3d26.3508702!4d-80.1028651'
            className='find-flags btn btn-primary'
            target='_blank'
            rel='noreferrer'
            color='info'
          >
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <FaStreetView style={{ marginRight: '4px' }} /> Find Them Here
            </div>
          </Button>
        </div>
        <Card className='main-copy'>
          <Card.Content>
            <h2 className='home-flags'>Flag Pickup!</h2>
            <h4 className='flag-pickup'>&#9135;&#9135;{data?.date}&#9135;&#9135;</h4>
            <p className='main-copy-text' dangerouslySetInnerHTML={{ __html: data?.content }} />

            <h4 style={{ marginTop: '50px' }}>Neighborhood Map with Zones</h4>
            <p style={{ marginBottom: '20px' }}>
              If you would like to volunteer to place flags please call us for ZONE availability @ 561-699-6117
            </p>
            <Card.Image src='https://assets.loganarnett.com/flagMap.png' />
          </Card.Content>
        </Card>
      </header>
    </div>
  )
}
