import React from 'react'
import { Container, Content, Footer } from 'react-bulma-components'
import { FaFacebookF, FaInstagram } from 'react-icons/fa'
import { Route, Switch } from 'react-router-dom'

import './App.scss'
import { About, Home, Nav } from './components'

const SiteFooter = () => (
  <Footer>
    <Container>
      <Content style={{ textAlign: 'center' }}>
        <p className='copyright'>&copy; 2021 The Sharon and Caron Group</p>
        <p className='social'>
          <a href='https://www.facebook.com/SharonCaronKW'>
            <FaFacebookF />
          </a>
          <a href='https://instagram.com/sharoncarongroup/'>
            <FaInstagram />
          </a>
        </p>
      </Content>
    </Container>
  </Footer>
)

function App() {
  return (
    <>
      <Nav />
      <Switch>
        <Route exact path='/'>
          <Home />
        </Route>
        <Route path='/about'>
          <About />
        </Route>
      </Switch>
      <SiteFooter />
    </>
  )
}

export default App
